export default {
  data() {
    return {
      openid: '',
      session_key: '',
      avatarUrl: '',
      gender: '',
      nickName: '',
      birthday:'',
      shareOpenid:'',
      encryptedData: '',
      errMsg: '',
      iv: ''
    };
  },
  methods: {
    // 微信登录
    login() {
      uni.login({
        provider: 'weixin',
        success: res => {
          // console.log(res)
          if (res.code) {
            uni.request({
              url: 'https://www.tailongshoudian.com/WNC/wxlogin/code2Session',
              method: "GET",
              header: {
                "Content-Type":
                  "application/x-www-form-urlencoded; charset=utf-8"
              },
              data: {
                code: res.code
              },
              success: res => {
                // console.log(res)
                if (res.data.success == true) {
                  this.openid = res.data.data.openid
                  this.session_key = res.data.data.session_key
                  uni.setStorageSync('openid',res.data.data.openid)
                  uni.setStorageSync('session_key', res.data.data.session_key)
                  this.getUser()
                }
              }
            })
          }
        }
      })
    },
    getUser() {
      uni.getUserInfo({
        success: res => {
          console.log(res)
          this.encryptedData = res.encryptedData
          this.errMsg = res.errMsg
          this.iv = res.iv
          uni.setStorageSync('avatarUrl',res.userInfo.avatarUrl)
          uni.setStorageSync('gender',res.userInfo.gender)
          uni.setStorageSync('nickName',res.userInfo.nickName)
          this.avatarUrl = res.userInfo.avatarUrl
          this.gender = res.userInfo.gender
          this.nickName = res.userInfo.nickName
          uni.request({
            url: 'https://www.tailongshoudian.com/WNC/wxlogin/saveUser',
            method: "POST",
            header: {
                "Content-Type": "application/x-www-form-urlencoded; charset=utf-8"
              },
            data: {
              gender: this.gender,
              nickName: this.nickName,
              openid: this.openid,
              imageUrl: this.avatarUrl,
              // encryptedData: this.encryptedData,
              // errMsg: this.errMsg,
              // iv: this.iv
            },
            success: res => {
              console.log(res)
              uni.navigateTo({
                url: "/pages/loginPhone/loginPhone"
              });
            }
          })
        }
      })
    }
  },
  computed: {
    isActive() {
      if (this.phone === '' || this.verificationVal === '') {
        return false
      }
      return true
    }
  },
};
